import React from 'react';

const AddArticleForm = () => {

    return(
        <>
        <div id="add-comment-form">
            <h1> Add Article</h1>
            <label>
                Title: 
                <input type="text" />
            </label>
            <label>
                Article Body:
                <textarea rows="10" cols="300" />
            </label>
            <button>Submit</button>
        </div>
        </>

    );
}

export default AddArticleForm;